import React from 'react'
import './Contact.css';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faLocationDot, faEnvelope, faPhone } from '@fortawesome/free-solid-svg-icons'

const Contact = () => {
  return (
    <section className='container-flued'>
      {/* <h1 style={{ fontFamily: "Khand, sans-serif" }} className="text-center">संपर्क</h1> */}
      <div className="contact-section">
        <div style={{ fontFamily: "Khand, sans-serif" }} className="address-box box">
          <div className='icon'>
            <FontAwesomeIcon icon={faLocationDot} color="#ffffff" size="3x"/>
          </div>
          <h1>पत्ता</h1>
          <p>अर्चनाताई तुषार पाटील जनसंपर्क कार्यालय,<br />भवानी पेठ क्षेत्रीय कार्यालयाजवळ, <br />भवानी पेठ काशेवाडी, पुणे- ४११०४२</p>
        </div>
        <div style={{ fontFamily: "Khand, sans-serif" }} className="email-box box">
          <div className='icon'>
            <FontAwesomeIcon icon={faEnvelope} color="#ffffff" size="3x"/>
          </div>
          <h1>इ-मेल</h1>
        </div>
        <div style={{ fontFamily: "Khand, sans-serif" }} className="call-box box">
          <div className='icon'>
            <FontAwesomeIcon icon={faPhone} color="#ffffff" size="3x"/>
          </div>
          <h1>दूरध्वनी</h1>
        </div>
      </div>
    </section>
  )
}

export default Contact