import React from 'react';
import './Blog.css';
import WorkData from './WorkData';
import image from './Images/slick images/slick1.jpg'; 
import { useParams } from 'react-router-dom';
import { useState } from 'react';
import { useEffect } from 'react';

const Blog = () => {

  const [data, setData] = useState(WorkData);
  const [blog, setBlog] = useState([]);
  const param = useParams();

  useEffect(() => {
    getData();
  },[]);

  const getData = () =>{
    const blogData = data.filter((item) => {
      return item.id == param.id;
    });
    setBlog(() => {
      return (blogData);
    })
  }


  return (
    <>
      <div className="blog-container">
        {  blog.map((item) => {
            return <>
                      <div className='blog-image'><img src={item.img} alt="blog image" /></div>
                      <span className='date'>30 Oct, 2022</span><span className='location'>   Location</span>
                      <p className='blog-desc'>{item.decs}</p>
                  </>
          })
        }
      </div>
    </>
  )
}

export default Blog;